$color-primary: #1F2D3D;
$color-text:  #3C4858;
$color-subtext: #8492A6;
$color-link: #6100ff;
$color-border: hsl(0, 0%, 85%);
$color-box-background: mix($color-primary, white, 4%);
$border-radius: 4px;
$font-family: ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";


  body, html {
    font-size: 17px;
  }
  
body {
  box-sizing: content-box;
  font-family: $font-family; 
  line-height: 1.7; 
  overflow-x: hidden;
  color: $color-text;
  font-size: 1rem; 

  @media (min-width: 820px) {
    // font-size: 1.2rem;
  }
}

time {
  display: block;
  color: $color-subtext;
  margin: 0.5em 0 1em;
}

footer {
  margin: 2em 0;
  font-size: 0.8em;
  color: $color-subtext;
  padding-top: 1em;
  a { 
    color: $color-subtext;
    border: 0;
  }
}
 

blockquote {
  padding: 1.5em;
  margin: 0;
  font-size: 0.88em;
  background: $color-box-background;
  border-radius: $border-radius;

  p {
    margin: 0;
  }
}

hr {
  width: 100%;
  border: 0;
  height: 1px;
  margin: 1.5em 0;
  background: $color-border;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: $color-primary;
  line-height: 1.3;
  margin-bottom: 0;
  padding-bottom: 0;
}

a {
  transition: background 300ms;
  padding: 0 0.1em;
  text-decoration: underline;
  color: $color-link;
  font-weight: 500;
  &:hover {
    background: rgba($color-link,0.1);
  }
  &:after {
    position: relative;
    top: -0.5em;
    font-size: 0.7em;
    content: "↗";
    color: #aaaaaa;
  }
  &.internal-link:after,
  &.footnote:after,
  &.reversefootnote:after {
    content: "";
  }
}

*:focus {
  // background: #ffe8bc !important;
  // color: black !important;
}

nav {
  margin: 1em 0 3em;
}

// #notes-entry-container {
//   display: grid;
//   grid-gap: 2em;
//   grid-template-areas:
//     "content"
//     "side";

//   @media (min-width: 700px) {
//     grid-template-columns: 3fr 1fr;
//     grid-template-areas: "content side";
//   }
// }

.backlink-box {
  background: $color-box-background;
  padding: 1em;
  border-radius: $border-radius;
}

code {
  background: #f5f5f5;
  padding: 0.1em 0.2em;
  border-radius: 4px;
}

.invalid-link {
  color: #444444;
  cursor: help;
  background: #fafafa;
  padding: 0 0.1em;
}

.invalid-link-brackets {
  color: #ccc;
  cursor: help;
}




