// 

body {
  padding: 20px;
  @media screen and (min-width:992px) {
    max-width: 650px;
    margin: 100px auto;
  }
}

main {
  padding: 50px 0;
}
 

.portrait { 
  padding-right: 50px;
  a {
    text-decoration: none;
    border: 0;
    &:hover {
      background: transparent;
    }
  }
  img { 
    border-radius: 5%;
    width: 60px;
    margin: 0;
    padding: 0;
    @media screen and (min-width: 768px) {
      width: 90px;
    }
  }
}

#header {
  h1, p {
    margin: 0;
    padding: 0;
    font-size: 100%;
    line-height: 1.6;
  }
  a {
    color: $color-primary;
    text-decoration: none;
    padding: 0;
    &:after {
      content: ""
    }
  }
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: 500;
}

article {
  header {
    margin-bottom: 100px;
  }
}